.header {
    width: 100%;
    margin: 0 auto;
    background: url(../../../../img/bgtop.png) repeat-x scroll left bottom transparent;
    background-size: 10px 135px;
    height: 135px;
    display: flex;
    justify-content: center;
    align-items: center;
    padding-bottom: 28px;
    position: relative;
}

.header--navbar {
    width: 900px;
    height: 60px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.header--button {
    background-image: url(../../../../img/icon_sprite@2x.png);
}

.header--label {
    color: #636363;
    text-align: center;
}

.header--loginUser {
    position: relative;
    margin-left: 35px;
}

.header--loginUser::before {
    width: 25px;
    content: "";
    background-image: url(../../../../img/icon_login25.png);
    background-repeat: no-repeat;
    position: absolute;
    left: -23px;
    bottom: 15px;
    height: 25px;
}

.header--navbar .navbar-dark .navbar-nav .nav-link.header--nav-link {
    color: rgb(34, 34, 34);
    display: block;
    font-family: Verdana, Arial, sans-serif;
    font-weight: bold;
    line-height: 30px;
    position: relative;
    text-decoration: none;
    text-transform: capitalize;
}

.navbar-dark .navbar-nav .nav-link.header--nav-link.active {
    color: red;
}

.navbar-expand-md.header--navbar-expand {
    display: inline-block;
    position: relative;
}

@media (min-width: 740px) and (max-width: 1023px) {
    .header {
        /* background: url(data:image/png;base64,)
            repeat-x scroll left bottom transparent; */
        background-size: 10px 100px;
        height: 100px;
        padding-bottom: 0;
    }
    .header--navbar {
        height: 40px;
        font-size: 12px;
    }
    .navbar-brand img {
        width: 100px;
    }

    .header-search-left img {
        width: 100px;
    }
    .header-search-right img {
        width: 80px;
    }
}

@media (max-width: 739px) {
    .header {
        /* background: url(data:image/png;base64,)
            repeat-x scroll left bottom transparent; */
        background-size: 10px 70px;
        height: 70px;
        padding-bottom: 0;
    }
    .navbar-brand img {
        width: 85px;
    }
    .header-search-left img {
        display: none;
    }
    .header-search-right img {
        display: none;
    }
    .header--navbar .navbar-dark .navbar-nav .nav-link.header--nav-link {
        font-size: 10px;
        line-height: 10px;
        padding-right: 8px;
        padding-left: 8px;
    }
    .navbar-nav {
        padding-top: 30px;
        position: relative;
    }
}

@media (max-width: 767.98px) {
    #collapsibleNavbar {
        position: absolute;
        z-index: 1;
        background-color: #fdf7dc;
        min-width: 200px;
        right: 0;
        box-shadow: 0 0 5px 2px #333;
        border-radius: 4px 0 0 4px;
    }
    .header--loginUser::before {
        left: -30px;
        bottom: 3px;
        width: 20px;
        height: 20px;
        background-size: cover;
    }
}
